<template>
  <div class="login">
    <div class="login-container">
      <!-- 登录部分 -->
      <login-form v-if="state === 'login'"/>
      <!-- 注册部分 -->
      <registry-form v-else/>
    </div>
  </div>
</template>

<script>
import LoginForm from './LoginForm'
import RegistryForm from './RegistryForm'

export default {
  name: 'Login',
  computed: {
    state () {
      return this.$route.params.state
    }
  },
  components: {
    LoginForm,
    RegistryForm
  }
}
</script>

<style lang="scss">
@import '../assets/style/mixin';
.login {
  height: 100%;
  text-align: center;
  @include vertical-center;
  &-container {
    display: inline-block;
    width: 1000px;
    height: 500px;
    background: #fff;
    border-radius: 4px;
    vertical-align: middle;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.06);
    @include vertical-center;
  }
  &-form {
    display: inline-block;
    width: 400px;
    padding: 20px 32px;
    vertical-align: middle;
    &__title {
      padding: 20px 0;
      font-size: 22px;
      color: #606266;
    }
  }
}
</style>



