import Vue from 'vue'
import Router from 'vue-router'
import axios from 'axios'
import NotFoundComponent from '@/components/404'
import Index from '@/components/Index'
import LoginRegistry from '@/components/LoginRegistry'
import Home from '@/components/Home'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
      redirect: '/home',
      children: [
        {
          path: 'home',
          name: 'home',
          component: Home
        }
      ]
    },
    {
      path: '/loginRegistry/:state',
      name: 'loginRegistry',
      component: LoginRegistry
    },
    {
      path: '*',
      name: '404',
      component: NotFoundComponent
    }
  ]
})

router.beforeEach(async (to, from, next) => {
  const res = await axios({ method: 'get', url: '/api/verifyLogin' })

  if (res.data.status === 1000) {
    // 已登录时的操作
    if (to.name !== 'loginRegistry') {
      next()
    } else {
      // 已登录时进入登录路由时
      // 跳转到首页路由
      router.push({
        name: 'index'
      })
    }
  } else if (res.data.status === 3000) {
    // 未登录时的操作
    if (to.name === 'loginRegistry') {
      next()
    } else {
      // 未登录时进入非登录路由时
      // 跳转到登录路由
      router.push({
        name: 'loginRegistry',
        params: { state: 'login' }
      })
    }
  }
})

export default router



