<template>
  <div class="home">
    <div class="home-container">
      <el-row class="mb10">
        <el-button type="primary" icon="el-icon-plus" size="small" @click="visible = true">添加</el-button>
      </el-row>
      <el-table
        :data="tableData"
        :height="tableHeight"
        style="width: 100%">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          prop="name"
          label="名称"
          width="180">
        </el-table-column>
        <el-table-column
          prop="price"
          label="价格"
          width="180">
          <template slot-scope="scope">
            <span>¥ {{ scope.row.price }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="sale_method"
          label="发售方式"
          width="180">
        </el-table-column>
        <el-table-column
          prop="sale_date"
          label="发售日期"
          width="180">
        </el-table-column>
        <el-table-column
          prop="series"
          label="系列">
        </el-table-column>
      </el-table>
    </div>
    <el-dialog
      title="添加数据"
      :visible.sync="visible"
      width="500px"
      @close="handleDialogClose">
      <el-form
        ref="addForm"
        :model="addForm"
        :rules="rules"
        label-width="80px">
        <el-form-item prop="name" label="名称">
          <el-input v-model="addForm.name"/>
        </el-form-item>
        <el-form-item prop="price" label="价格">
          <el-input v-model.number="addForm.price"/>
        </el-form-item>
        <el-form-item prop="sale_method" label="发售方式">
          <el-input v-model="addForm.sale_method"/>
        </el-form-item>
        <el-form-item prop="sale_date" label="发售日期">
          <el-date-picker
            v-model="addForm.sale_date"
            type="month"
            placeholder="选择日期"
            :editable="false"
            value-format="yyyy-MM">
          </el-date-picker>
        </el-form-item>
        <el-form-item prop="series" label="系列">
          <el-input v-model="addForm.series"/>
        </el-form-item>
      </el-form>
      <el-row slot="footer">
        <el-button type="primary" size="medium" @click="handleAddFormSubmit">确定</el-button>
        <el-button size="medium" @click="visible = false">取消</el-button>
      </el-row>
    </el-dialog>
  </div>
</template>

<script>
import { request } from '@/assets/js/util'

export default {
  name: 'Home',
  data () {
    return {
      visible: false,
      tableHeight: null,
      tableData: [],
      addForm: {
        name: '',
        price: null,
        sale_method: '',
        sale_date: '',
        series: '',
        type: 'METALBUILD'
      },
      rules: {
        name: [{ required: true, message: '请输入名称', trigger: 'blur' }],
        price: [
          { required: true, message: '请输入价格', trigger: 'blur' },
          { type: 'number', message: '请输入数字', trigger: 'blur' }
        ],
        sale_method: [{ required: true, message: '请输入发售方式', trigger: 'blur' }],
        sale_date: [{ required: true, message: '请输入发售日期', trigger: 'blur' }],
        series: [{ required: true, message: '请输入系列', trigger: 'blur' }]
      }
    }
  },
  methods: {
    initData () {
      request({
        method: 'get',
        url: '/api/getList'
      }).then(res => {
        if (res.status === 1000 && res.success) {
          this.tableData = res.data
        }
      })
    },
    setTableHeight () {
      const bodyHeight = document.body.clientHeight
      this.tableHeight = bodyHeight - 50 - 20 - 42
    },
    handleDialogClose () {
      this.$refs.addForm.resetFields()
    },
    handleAddFormSubmit () {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          request({
            method: 'post',
            url: '/api/addList',
            data: {
              ...this.addForm
            }
          }).then(res => {
            if (res.status === 1000 && res.success) {
              this.visible = false
              this.$refs.addForm.resetFields()
              this.initData()
            }
          })
        } else {
          return false
        }
      })
    }
  },
  created () {
    this.setTableHeight()
    this.initData()
  }
}
</script>

<style lang="scss">
.home {
  width: 100%;
  height: 100%;
  padding: 10px;
  box-sizing: border-box;
  &-container {
  }
}
</style>



