<template>
  <div class="layout">
    <div class="layout-header">
      <div class="layout-header__logo"></div>
      <div class="layout-header__user">
        <span>Hello {{name}}</span><a href="javascript:;" class="layout-header__loginOut" @click="handleLoginOut">退出登录</a>
      </div>
    </div>
    <div class="layout-left">
      <div class="layout-left__logo"></div>
      <ul class="layout-left__content">
        <li class="layout-left__item" :class="{active: item.active}" v-for="item in menuList" :key="item.id">
          <i class="iconfont" :class="item.icon"></i>
          <span>{{item.text}}</span>
        </li>
      </ul>
    </div>
    <div class="layout-content">
      <router-view/>
    </div>
  </div>
</template>

<script>
import { request } from '@/assets/js/util'

export default {
  name: 'Index',
  data () {
    return {
      name: '',
      menuList: [
        { id: 1, text: '首页', icon: 'icon-homepage', active: true }
      ]
    }
  },
  methods: {
    handleLoginOut () {
      request({
        method: 'get',
        url: '/api/loginOut'
      }).then(res => {
        if (res.status === 1000) {
          this.$router.push({
            name: 'loginRegistry',
            params: {
              state: 'login'
            }
          })
        }
      })
    }
  },
  created () {
    request({
      method: 'get',
      url: '/api/getInfor'
    }).then(res => {
      if (res.status === 1000 && res.success) {
        this.name = res.data.name
      }
    })
  }
}
</script>

<style lang="scss">
@import '../assets/style/mixin';
.layout {
  height: 100%;
  &-header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 20;
    width: 100%;
    height: 50px;
    color: rgba(0,0,0,.65);
    background: #fff;
    box-shadow: 0 1px 4px rgba(0,21,41,.08);
    &__user {
      float: right;
      margin-right: 30px;
      line-height: 50px;
    }
    &__loginOut {
      margin-left: 30px;
      vertical-align: top;
      font-size: 12px;
      &:hover {
        color: #409EFF;
      }
    }
  }
  &-left {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 30;
    width: 180px;
    height: 100%;
    background: rgb(0, 21, 41);
    box-shadow: 2px 0 6px rgba(0,21,41,.35);
    &__logo {
      height: 50px;
      background: url('../assets/logo.png') no-repeat;
      background-color: #002140;
      background-size: auto 30px;
      background-position: center;
    }
    &__content {
      padding: 10px 0;
    }
    &__item {
      height: 40px;
      padding-left: 30px;
      color: rgba(255, 255, 255, .8);
      font-size: 14px;
      line-height: 40px;
      cursor: pointer;
      &:hover {
        color: rgba(255, 255, 255, 1);;
      }
      &.active {
        background: #1890ff;
      }
      .iconfont {
        vertical-align: middle;
      }
      span {
        vertical-align: middle;
      }
    }
  }
  &-content {
    height: 100%;
    padding: 50px 0 0 180px;
    box-sizing: border-box;
  }
}
</style>



