<template>
  <el-form class="login-form" label-width="80px" ref="loginForm" :model="loginForm" :rules="loginRules">
    <h1 class="login-form__title">账号登录</h1>
    <el-form-item label="用户名" prop="account">
      <el-input v-model="loginForm.account" @keyup.native.enter="handleLogin"></el-input>
    </el-form-item>
    <el-form-item label="密码" prop="password">
      <el-input type="password" v-model="loginForm.password" @keyup.native.enter="handleLogin"></el-input>
    </el-form-item>
    <el-form-item label="验证码" prop="captcha">
      <el-input class="login-form__captcha" v-model.number="loginForm.captcha" @keyup.native.enter="handleLogin"></el-input>
      <img class="login-form__captchaPng" :src="`/api/captcha.png?random=${random}`" @click="updateCaptcha" title="点击换一张">
    </el-form-item>
    <el-row>
      <el-button type="primary" @click="handleLogin">登录</el-button>
      <el-button @click="goRegistry">注册</el-button>
    </el-row>
  </el-form>
</template>

<script>
import { request } from '@/assets/js/util'

export default {
  name: 'LoginForm',
  data () {
    return {
      random: parseInt(Math.random() * 100000),
      loginForm: {
        account: '',
        password: '',
        captcha: ''
      },
      loginRules: {
        account: [
          { required: true, message: '请输入账号', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ],
        captcha: [
          { required: true, message: '请输入验证码', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 登录处理
    handleLogin () {
      const { loginForm } = this.$refs

      loginForm.validate(async valid => {
        if (valid) {
          const res = await request({
            method: 'post',
            url: '/api/login',
            data: {
              ...this.loginForm
            }
          })

          if (res.status === 1000 && res.success) {
            this.$router.push({
              name: 'index'
            })
          } else {
            loginForm.clearValidate()
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        } else {
          return false
        }
      })
    },
    goRegistry () {
      this.$router.replace({
        name: 'loginRegistry',
        params: {
          state: 'registry'
        }
      })
    },
    updateCaptcha () {
      this.random = parseInt(Math.random() * 100000)
    }
  }
}
</script>

<style lang="scss">
.login {
  &-form {
    &__captcha {
      float: left;
      width: 200px;
    }
    &__captchaPng {
      float: left;
      height: 40px;
      margin-left: 15px;
      border: 1px solid #dcdfe6;
      cursor: pointer;
      box-sizing: border-box;
    }
  }
}
</style>



