<template>
  <el-form class="login-form" label-width="80px" :model="registryForm" :rules="registryRules" ref="registryForm" status-icon>
    <h1 class="login-form__title">注册账号</h1>
    <el-form-item label="用户名" prop="account">
      <el-input type="text" v-model="registryForm.account"></el-input>
    </el-form-item>
    <el-form-item label="密码" prop="password">
      <el-input type="password" v-model="registryForm.password"></el-input>
    </el-form-item>
    <el-form-item label="确认密码" prop="checkPass">
      <el-input type="password" v-model="registryForm.checkPass"></el-input>
    </el-form-item>
    <el-row>
      <el-button type="primary" @click="handleRegistry">立即注册</el-button>
      <el-button @click="goLogin">返回登录</el-button>
    </el-row>
  </el-form>
</template>

<script>
import { request } from '@/assets/js/util'

export default {
  data () {
    // 注册时验证账号
    var accountValidate = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入账号'))
      } else {
        request({
          method: 'get',
          url: '/api/checkAccount',
          params: {
            account: value
          }
        }).then(res => {
          if (res.status === 1000 && res.success) {
            callback()
          } else {
            callback(new Error(res.msg))
          }
        })
      }
    }
    // 注册时验证密码
    var passValidate = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.registryForm.checkPass !== '') {
          this.$refs.registryForm.validateField('checkPass')
        }
        callback()
      }
    }
    // 注册时验证密码检查
    var checkPassValidate = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.registryForm.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }

    return {
      registryForm: {
        account: '',
        password: '',
        checkPass: ''
      },
      registryRules: {
        account: [
          { validator: accountValidate, trigger: 'blur' }
        ],
        password: [
          { validator: passValidate, trigger: 'blur' }
        ],
        checkPass: [
          { validator: checkPassValidate, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    handleRegistry () {
      const { registryForm } = this.$refs
      const { account, password } = this.registryForm

      registryForm.validate((valid) => {
        if (valid) {
          request({
            method: 'post',
            url: '/api/registry',
            data: {
              account,
              password
            }
          }).then(res => {
            if (res.status === 1000 && res.success) {
              this.$router.push({ name: 'index' })
            } else {
              this.$message({
                showClose: true,
                message: res.msg,
                type: 'error'
              })
            }
          })
        } else {
          return false
        }
      })
    },
    goLogin () {
      this.$router.replace({
        name: 'loginRegistry',
        params: {
          state: 'login'
        }
      })
    }
  }
}
</script>



