import axios from 'axios'
import router from '@/router'

export function request (options = {}) {
  return new Promise((resolve, reject) => {
    axios(options).then(res => {
      if (res.status === 200) {
        if (res.data.status === 3000) {
          router.push({
            name: 'loginRegistry',
            params: { state: 'login' }
          })
        } else {
          resolve(res.data)
        }
      }
    }).catch(err => {
      this.$message({
        showClose: true,
        message: `请求错误: ${err}`,
        type: 'error'
      })
    })
  })
}



